/* styles/Contact.scss */
@import 'variables';

#contato{
    h2{
        margin-bottom: 10%;

        @media screen and (max-width: 768px) {
            margin-bottom: 8%;
        }

        @media screen and (max-width: 480px) {
            margin-bottom: 6%;
        }
    }

    p{
        margin: 0;
        font-family: $fonte-primaria;
        color: $cor-secundaria;
        font-size: clamp(8px, 1.5vw, 1.5vw);

        @media screen and (max-width: 768px) {
            font-size: clamp(12px, 2vw, 2vw);
        }

        @media screen and (max-width: 480px) {
            font-size: clamp(13px, 2.3vw, 2.3vw);
        }
    }

    .contatos-container{
        display: flex;
        flex-direction: row;

        @media screen and (max-width: 768px) {
            flex-direction: column;
        }
    }

    .contatos{
        display: flex;
        justify-content: center;
        gap: clamp(20px, 5vw, 40px);
        flex-wrap: wrap;

        @media screen and (max-width: 768px) {
            flex-direction: column;
            align-items: center;
            gap: clamp(15px, 4vw, 30px);
        }

        @media screen and (max-width: 480px) {
            gap: clamp(10px, 3vw, 20px);
        }
    }

    form{
        display: flex;
        flex-direction: column;
        gap: 1vw;
    }

    
    input, textarea{
        width:28vw;
        height: 3vw;
        border-radius: 0.8vw;
        border: solid 0.2vw $cor-secundaria;
        background-color: $cor-terciaria;
        color: white;
        outline: none;
        font-size: clamp(10px, 1.2vw, 1.2vw);
        padding-left: 4%;
        resize: none;
        box-sizing: border-box;

        @media screen and (max-width: 768px) {
            width: 60vw;
            height: 4vw;
            min-height: 35px;
            border-radius: 1vw;
            font-size: clamp(16px, 2vw, 2vw);
        }

        @media screen and (max-width: 480px) {
            width: 85vw;
            height: 5vw;
            min-height: 40px;
            border-radius: 1.2vw;
            font-size: clamp(18px, 2.5vw, 2.5vw);
            padding-left: 5%;
        }
    }

    textarea{
        height: 5vw;

        @media screen and (max-width: 768px) {
            height: 8vw;
            min-height: 80px;
        }

        @media screen and (max-width: 480px) {
            height: 12vw;
            min-height: 100px;
        }
    }


    ::placeholder {
        color: $cor-secundaria;  }
  

    button{
        width:29.5vw;
        height: 3vw;
        border-radius: 0.8vw;
        border: solid 0.2vw $cor-secundaria;
        background-color: $cor-terciaria;
        color: white;
        font-size: clamp(10px, 1.2vw, 1.2vw);
        box-sizing: border-box;

        @media screen and (max-width: 768px) {
            width: 60vw;
            height: 4vw;
            min-height: 40px;
            border-radius: 1vw;
            font-size: clamp(16px, 2vw, 2vw);
        }

        @media screen and (max-width: 480px) {
            width: 85vw;
            height: 5vw;
            min-height: 45px;
            border-radius: 1.2vw;
            font-size: clamp(18px, 2.5vw, 2.5vw);
        }
    }

    button:hover{
        background-color: $cor-secundaria;
        color: $cor-terciaria;
        cursor: pointer;
        
    }

  
    .icons{
        display: flex;
        gap: 2vw;
        height: 3vw;
        align-items: center;
        justify-content: center;

        @media screen and (max-width: 768px) {
            gap: 3vw;
            height: 4vw;
        }

        @media screen and (max-width: 480px) {
            gap: 4vw;
            height: 5vw;
        }
    }

    a svg.bi-github {
        fill: #F22987; 
        width: 3vw;
        height: 3vw;

        @media screen and (max-width: 768px) {
            width: 4vw;
            height: 4vw;
            min-width: 24px;
            min-height: 24px;
        }

        @media screen and (max-width: 480px) {
            width: 5vw;
            height: 5vw;
            min-width: 28px;
            min-height: 28px;
        }
    }
    
    a svg.bi-linkedin {
        fill: #F22987; 
        width: 3vw;
        height: 3vw;

        @media screen and (max-width: 768px) {
            width: 4vw;
            height: 4vw;
            min-width: 24px;
            min-height: 24px;
        }

        @media screen and (max-width: 480px) {
            width: 5vw;
            height: 5vw;
            min-width: 28px;
            min-height: 28px;
        }
    }

    a svg.bi-whatsapp {
        fill: #F22987; 
        width: 3vw;
        height: 3vw;

        @media screen and (max-width: 768px) {
            width: 4vw;
            height: 4vw;
            min-width: 24px;
            min-height: 24px;
        }

        @media screen and (max-width: 480px) {
            width: 5vw;
            height: 5vw;
            min-width: 28px;
            min-height: 28px;
        }
    }

    img{
        margin-top: 6%;
        width: 15vw;
        object-fit: contain;

        @media screen and (max-width: 768px) {
            margin-top: 4%;
            width: 20vw;
            min-width: 100px;
        }

        @media screen and (max-width: 480px) {
            margin-top: 3%;
            width: 25vw;
            min-width: 80px;
        }
    }

    svg:hover{
        scale: 1.1;
        filter: drop-shadow(1px 1px 4px $cor-primaria);
    }
    
    .direita-contatos {
        @media screen and (max-width: 768px) {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
        }
    }    
  
}